









































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static:'',
			type:0,
			malllist:[
			],
			guigetype:[],
			num: 0,
			mallinf: {},
			mallprice:0,
			barlist:[],
			pop:false,
			clientGoods:{}
		}
	},
	onLoad(opt) {
		this.static=ut.static;
		this.req_detail(opt._id)
	},
	methods: {
		cg_type(type){
			this.type=type;
			this.mallinf =this.guigetype[type]
		},
		cg_pop(){
			if(!this.pop&&!this.num){
				return;
			}
			this.pop=!this.pop;
		},
		go_build_pay(){
			wx.navigateTo({
				url: '../build/pay'
			})
		},
		minus(index){
			if(this.num==0)return;
			this.num-=1;
			this.mallinf.num-=1;
			this.mallprice=(Number(this.mallprice)-Number(this.mallinf.price)).toFixed(2);
			this.barlist[index||0]=this.mallinf;
			if(this.mallinf.num==0){
				this.barlist.splice(index||0,1);
				this.pop=false;
			}
		},
		add(index){
			this.num+=1;
			this.mallinf.num+=1;
			this.mallprice=(Number(this.mallprice)+Number(this.mallinf.price)).toFixed(2);
			this.barlist[index||0]=this.mallinf;
		},
		cleanbar(){
			this.num=0;
			this.mallinf.num=0;
			this.mallprice=0;
			this.barlist[0]=[];
			this.pop=false;
		},
		req_detail(id){
			ut.request({
				data: {
					goodsid:id
				},
				url: "goods/goodsdetail"
			}).then(data=>{
				this.clientGoods=data.clientGoods;
				if(data.clientGoodsSpecList[0]){
					data.clientGoodsSpecList.forEach(item =>{
						item.num=0;
					})
					this.mallinf =data.clientGoodsSpecList[0]
				}
				this.guigetype=data.clientGoodsSpecList;
			})
		},
		filterdate(){
			let data=[];
			console.log(this.barlist)
			for(let i=0;i<=this.barlist.length;i++){
				console.log(this.barlist[i])
				data[i]={
					goodsid:this.barlist[i].goodsId,
					num:this.barlist[i].num,
					specId:this.barlist[i].id
				}
			}
			return data;
		},
		req_cartadd(){
			console.log(this.barlist)
			const data=this.filterdate();
			ut.request({
				data: data,
				url: "cart/add"
			}).then(data=>{})
		}
	}
}



